import React from 'react';
import { useMetadata } from '../../providers/MetadataProvider';
import { Link } from 'react-router-dom';
import { DotLoading } from '../../components/Loading/Dot';
import './Home.css';



export const HomeView = () => {
    const md = useMetadata();
    const [loading, setloading] = React.useState(true);

    React.useEffect(() => {
        if (md) {
            setloading(false);
            return
        }

        setloading(true);
    }, [md, setloading])

    return (
        <div className="home">
            <div className="logo-box">
                <p className="logo-block" style={{ left: '22%' }}></p>
                <p className="logo-block" style={{ left: '42%' }}></p>
                <p className="logo-block" style={{ left: '62%' }}></p>
                <h1 className="title">Nation Origins</h1>
            </div>


            {loading ? <DotLoading className="home-loading" />
                :
                <Link className="explore-btn" to='/collection/Nation-Origins-α'>探索α</Link>
            }

        </div>
    )
}